export const reducerApp = (state, action) => {
    switch(action.type) {
        case 'updateState':
            return {
                ...state,
                ...action.payload
            };
        case 'updateAccessGroup':
            return {
                ...state,
                accessGroup: {...action.payload}
            };
        case 'updateSettings':
            return {
                ...state,
                user: {
                    ...state.user,
                    settings: {...action.payload}
                }
            };
        default:
            return state
    }
};
